<template>
  <div>
    <!-- Project Nav -->

    <ProjectNav
      v-if="!mobile"
      :loaded="dataLoaded"
      :title="currentTitle"
      :date="currentDate"
      :projects="projects"
      @info="scrollLeft"
      @changeproject="changeProject"
    />

    <!-- Content -->

    <div v-if="mobile" class="mobile-project-item">
      <ProjectContent
        v-for="(project, index) in projects"
        :mobile="mobile"
        :key="index"
        :index="index"
        :loaded="dataLoaded"
        :info="showInfo"
        :current="project"
      />
    </div>
    <ProjectContent
      v-else
      :mobile="mobile"
      :key="currentProject"
      :loaded="dataLoaded"
      :info="showInfo"
      :current="currentProject"
    />
  </div>
</template>
<script>
export default {
  data() {
    return {
      showInfo: false,
      dataLoaded: false,
      projects: [],
      currentProject: [],
      mobile: true,
    };
  },
  methods: {
    getContent(uid) {
      this.$prismic.client.getByUID("category", uid).then((response) => {
        this.projects = response.data.body;
        this.currentProject = this.projects[0];
        this.dataLoaded = true;
      });
    },
    scrollLeft(e) {
      this.showInfo = e;
    },
    changeProject(e) {
      this.dataLoaded = false;
      this.currentProject = e;
      this.dataLoaded = true;
    },
    screenSize() {
      window.innerWidth < 768 ? (this.mobile = true) : (this.mobile = false);
    },
  },
  computed: {
    currentTitle() {
      if (this.dataLoaded) {
        return this.currentProject.primary.project_title[0].text;
      } else return "";
    },
    currentDate() {
      if (this.dataLoaded) {
        return this.currentProject.primary.date;
      } else return "";
    },
  },
  created() {
    this.screenSize();
    this.getContent(this.$route.params.uid);
    window.addEventListener("resize", this.screenSize);
  },
};
</script>
